<template>
  <div class="base-timer">
    <svg class="base-timer__svg" viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
      <g class="base-timer__circle">
        <circle class="base-timer__path-elapsed" cx="50" cy="50" r="45"></circle>
        <path
          :stroke-dasharray="circleDasharray"
          class="base-timer__path-remaining"
          :class="remainingPathColor"
          d="
            M 50, 50
            m -45, 0
            a 45,45 0 1,0 90,0
            a 45,45 0 1,0 -90,0
          "
        ></path>
      </g>
    </svg>
    <span class="base-timer__label">{{ formattedTimeLeft }}</span>
<!-- <button class="btn button-reset" v-on:click.prevent="forceUpdate">Restart</button> -->
    <div class="buttons-div">
      <h2>Choose your alarm </h2>
<button class="btn btn-primary" v-on:click.prevent="changeVideoVintage">Vintage</button>
<button class="btn btn-primary" v-on:click.prevent="changeVideoSamurai">Samurai</button>
<button class="btn btn-primary" v-on:click.prevent="changeVideoRussian">Russian</button>
</div>
<div class="youtube-div">
<video-embed ref="youtube" :params="{autoplay: 1,fs: 0, modestbranding: 1}" src= "https://www.youtube.com/watch?v=tb0B3auGbtA"></video-embed>
</div>
    </div>

</template>

<script>
const FULL_DASH_ARRAY = 283;
const WARNING_THRESHOLD = 10;
const ALERT_THRESHOLD = 5;

const COLOR_CODES = {
  info: {
    color: "green"
  },
  warning: {
    color: "orange",
    threshold: WARNING_THRESHOLD
  },
  alert: {
    color: "red",
    threshold: ALERT_THRESHOLD
  }
};

const TIME_LIMIT = 1860;

import Embed from 'v-video-embed'
import Vue from 'vue'

export default {

  data() {
    return {
      timePassed: 0,
      timerInterval: null,
    };
  },

  computed: {
    circleDasharray() {
      return `${(this.timeFraction * FULL_DASH_ARRAY).toFixed(0)} 283`;
    },

    formattedTimeLeft() {
      const timeLeft = this.timeLeft;
      const minutes = Math.floor(timeLeft / 60);
      let seconds = timeLeft % 60;

      if (seconds < 10) {
        seconds = `0${seconds}`;
        
      }

      return `${minutes}:${seconds}`;
    },

    timeLeft() {
      return TIME_LIMIT - this.timePassed;
    },

    timeFraction() {
      const rawTimeFraction = this.timeLeft / TIME_LIMIT;
      return rawTimeFraction - (1 / TIME_LIMIT) * (1 - rawTimeFraction);
    },

    remainingPathColor() {
      const { alert, warning, info } = COLOR_CODES;

      if (this.timeLeft <= alert.threshold) {
        return alert.color;
        
      } else if (this.timeLeft <= warning.threshold) {
        return warning.color;
      } else {
        return info.color;
      }
    }
  },

  watch: {
    timeLeft(newValue) {
      if (newValue === 0) {
        this.onTimesUp();
        this.showYoutube();

      }
    }
  },

  mounted() {
    this.startTimer();
  },
  methods: {
    onTimesUp() {
      clearInterval(this.timerInterval);
    },

    startTimer() {
      this.timerInterval = setInterval(() => (this.timePassed += 1), 1000);
    },

    changeVideoRussian() {
        this.$refs.youtube.src = "https://www.youtube.com/watch?v=9sjyiSSi4IQ";
 
    },
    changeVideoVintage() {
    this.$refs.youtube.src = "https://www.youtube.com/watch?v=tb0B3auGbtA"; 

    },
    changeVideoSamurai() {
    this.$refs.youtube.src = "https://www.youtube.com/watch?v=BwuwYELL-tg"; 
    },

    showYoutube() {
    Vue.use(Embed);
    },
    // forceUpdate (){
    //   return{
    //   timePassed: 0,
    //   seconds :1860
    //   }
    // }

  }
};
</script>

